                    footer {
                        background-color: rgb(1, 132, 117);
                        padding: 10px;
                    }
                    
                    .footer {
                        &__logo {
                            img {
                                width: 50px;
                                object-fit: cover;
                                border-radius: 6px;
                            }
                            span {
                                color: white;
                                font-size: 15px;
                            }
                        }
                        &__nav {
                            li {
                                list-style-type: none;
                                margin-top: 5px;
                            }
                            a {
                                text-decoration: none;
                                padding: 6px;
                                color: white;
                                transition: .1s ease-in;
                                &:hover {
                                    background-color: white;
                                    border-radius: 6px;
                                    color: rgb(1, 132, 117);
                                }
                            }
                        }
                        &__social {
                            li {
                                list-style-type: none;
                                a {
                                    text-decoration: none;
                                    color: white;
                                    padding: 5px;
                                    margin-left: 5px;
                                    &:hover {
                                        background-color: white;
                                        border-radius: 6px;
                                        color: rgb(1, 132, 117);
                                    }
                                }
                                i {
                                    color: white;
                                    font-size: 18px;
                                }
                            }
                        }
                    }
                    
                    @media screen and (max-width: 768.99px) {
                        .footer {
                            &__logo {
                                span {
                                    font-size: 13px;
                                }
                            }
                            &__nav {
                                ul {
                                    padding-left: 0;
                                }
                            }
                        }
                    }