        .header {
            // lightblue
            box-shadow: 0 0 12px 0 rgb(1, 132, 117);
            background-color: rgb(1, 132, 117);
            &__content {
                display: flex;
                justify-content: space-around;
            }
            &__icons {
                i {
                    color: #fff;
                    display: none;
                }
            }
            &__logo_bar {
                a {
                    text-decoration: none;
                    color: white;
                    animation: fooocus 2s ease-in 1s infinite;
                }
            }
            @keyframes fooocus {
                0% {
                    opacity: 0;
                }
                25% {
                    opacity: 1;
                }
                50% {
                    opacity: 0;
                }
                75% {
                    opacity: 1;
                }
                100% {
                    opacity: 0;
                }
            }
            // &__navbar_bar {
            // }
            &__list {
                display: flex;
                li {
                    margin-left: 15px;
                    list-style-type: none;
                    a {
                        text-decoration: none;
                        color: white;
                        padding: 6px 10px;
                        transition: .2s ease-in;
                        &:hover {
                            color: rgb(1, 132, 117);
                            border-radius: 6px;
                            background-color: white;
                        }
                    }
                }
            }
        }
        
        @media screen and (max-width: 991.99px) {
            .header {
                &__list {
                    display: none;
                }
                &__icons {
                    i {
                        display: block;
                    }
                }
            }
        }