.nihad {
    &__images {
        img {
            width: 100%;
            box-shadow: 2px 3px 4px 3px grey;
        }
    }
    &__infos {
        h2 {
            font-size: 45px;
            color: #018475;
        }
        p {
            font-size: 22px;
        }
        text-align: center;
        align-self: center;
    }
    &__blocks {
        h4 {
            text-align: center;
            color: #018475;
        }
        flex-direction:column;
    }
    &__amulets {
        &:hover {
            img {
                opacity: 1;
            }
        }
    }
    &__imgss {
        img {
            width: 100%;
            border-radius: 4px;
            opacity: 0.8;
            transition: .2s ease-in;
        }
    }
    &__prices {
        text-align: center;
        display: none;
        border-radius: 4px;
        padding: 5px;
        background-color: #018475;
        span {
            color: #fff;
        }
    }
    &__buttons {
        text-align: center;
        button {
            background-color: #018475;
            border: none;
            border-radius: 4px;
            padding: 6px 16px;
            color: #fff;
        }
    }
    &__terapi-texts {
        border: 2px solid #018475;
        border-radius: 7px;
        padding: 6px;
    }
    &__terapi-prs {
        p {
            background-color: #018475;
            padding: 6px;
        }
    }
}