                .services {
                    // #208100
                    &__head {
                        h3 {
                            font-family: serif;
                            span {
                                color: rgb(1, 132, 117);
                                font-size: 18px;
                                font-family: 'Courier New', Courier, monospace;
                            }
                        }
                    }
                    &__box {
                        .carts {
                            background-color: rgb(1, 132, 117);
                            padding: 10px;
                            color: white;
                            border-radius: 6px;
                            &__heading {
                                text-align: center;
                                i {
                                    font-size: 30px;
                                }
                                h6 {
                                    font-family: cursive;
                                }
                            }
                            &__content {
                                img {
                                    width: 100%;
                                    opacity: 0.7;
                                    height: 180px;
                                    border-radius: 20px;
                                    object-fit: cover;
                                    padding: 10px 0px 10px 0px;
                                }
                            }
                        }
                    }
                }
                
                //Секция о нас старт
                .about {
                    h3 {
                        font-family: serif;
                        span {
                            color: rgb(1, 132, 117);
                            font-size: 18px;
                            font-family: 'Courier New', Courier, monospace;
                        }
                    }
                }
                
                .nihad {
                    hr {
                        color: rgb(1, 132, 117);
                    }
                    text-align: center;
                    &__photos {
                        img {
                            border-radius: 200px;
                            width: 250px;
                        }
                    }
                    &__buttons {
                        a {
                            background-color: rgb(1, 132, 117);
                            color: white;
                            transition: .2s ease-in-out;
                            border-radius: 4px;
                            padding: 10px;
                            text-decoration: none;
                            &:hover {
                                color: rgb(1, 132, 117);
                                background-color: white;
                                border: 2px solid rgb(1, 132, 117);
                            }
                        }
                    }
                }
                
                .huseyn {
                    text-align: center;
                    &__photos {
                        img {
                            border-radius: 200px;
                            width: 236.5px;
                        }
                    }
                    &__buttons {
                        a {
                            background-color: rgb(1, 132, 117);
                            color: white;
                            transition: .2s ease-in-out;
                            border-radius: 4px;
                            padding: 10px;
                            text-decoration: none;
                            &:hover {
                                color: rgb(1, 132, 117);
                                background-color: white;
                                border: 2px solid rgb(1, 132, 117);
                            }
                        }
                    }
                    hr {
                        color: rgb(1, 132, 117);
                    }
                }
                
                // constulting starts scss
                // mains consulting background stil 
                .images__t {
                    background: url(../img/psy.jpg);
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-attachment: fixed;
                    min-height: 600px;
                }
                
                .added1,
                .added2,
                .added3,
                .added4 {
                    display: none;
                }
                
                .consulting {
                    &__head {
                        h2 {
                            color: #fff;
                            text-align: center;
                            padding-top: 26px;
                            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
                            span {
                                color: #fff;
                            }
                        }
                    }
                    &__carts {
                        background: linear-gradient(to right, rgb(1, 132, 117) 20%, rgb(1, 90, 80) 80%);
                        padding: 10px;
                        margin-top: 25px;
                        color: #fff;
                        text-align: center;
                        border-radius: 10px;
                        min-height: 400px;
                        transition: 0.2s ease-in;
                        span {
                            font-size: 24px;
                        }
                        &:hover {
                            box-shadow: 0px 2px 4px 3px rgb(172, 172, 172);
                        }
                    }
                    &__buttons {
                        button {
                            background-color: #fff;
                            border: none;
                            border-radius: 4px;
                            padding: 3px 6px;
                            transition: .2s ease-in;
                            &:hover {
                                background-color: rgb(1, 132, 117);
                                color: #fff;
                                border: 1px solid #fff;
                            }
                        }
                        .backs1,
                        .backs2,
                        .backs3,
                        .backs4 {
                            display: none;
                        }
                    }
                }
                
                // clients css
                .clients {
                    background: url(../img/fon.jpg);
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-attachment: fixed;
                    min-height: 400px;
                    &__inner {
                        padding: 15px;
                        background-color: #fff;
                        border-radius: 4px;
                        opacity: 0.6;
                        min-height: 250px;
                        text-align: center;
                        padding-top: 100px;
                        color: rgb(1, 132, 117);
                        transition: .2s ease-in;
                        &:hover {
                            opacity: 0.9;
                            box-shadow: 0px 0px 2px 8px #fff;
                        }
                    }
                    &__number {
                        p {
                            font-size: 46px;
                        }
                    }
                }
                
                // cancels default padding bootstrap
                .container-fluid {
                    padding-left: 0px !important;
                    padding-right: 0px !important;
                }
                
                // cancels default padding bootstrap
                // aside menu style
                .asaide {
                    &__menu {
                        // display: none;
                        position: relative;
                        background: linear-gradient(to right, rgb(1, 132, 117) 60%, rgb(9, 134, 120) 30%);
                        width: 25%;
                        height: 100vh;
                        position: fixed;
                        top: 0;
                        right: 100%;
                        overflow-y: auto;
                        z-index: 2;
                    }
                    &__list {
                        width: 100%;
                        margin-top: 80px;
                        text-align: center;
                        li {
                            list-style-type: none;
                            margin-top: 10px;
                            a {
                                color: #fff;
                                font-size: 30px;
                                border-radius: 5px;
                                transition: .3s ease;
                                text-decoration: none;
                                padding: 5px 25px;
                                &:hover {
                                    background-color: #fff;
                                    color: rgb(1, 132, 117);
                                }
                            }
                        }
                    }
                    &__our {
                        width: 100%;
                        margin-top: 30px;
                        text-align: center;
                        ul li {
                            list-style-type: none;
                            a {
                                color: #fff;
                                text-decoration: none;
                                font-size: 25px;
                                padding: 6px;
                                transition: .2s ease-in-out;
                                &:hover {
                                    box-shadow: 3px 4px 2px 2px rgb(90, 89, 89);
                                }
                            }
                        }
                    }
                    &__social {
                        width: 100%;
                        text-align: center;
                        ul {
                            display: flex;
                            justify-content: center;
                            flex-wrap: wrap;
                            margin-top: 60px;
                        }
                        ul li {
                            list-style-type: none;
                            margin-left: 13px;
                            i {
                                font-size: 20px;
                                a {
                                    font-size: 17px;
                                    color: #fff;
                                    text-decoration: none;
                                    margin-left: 13px;
                                    &:hover {
                                        box-shadow: 3px 4px 2px 2px rgb(90, 89, 89);
                                    }
                                }
                            }
                        }
                    }
                }
                
                // responsive add jquery menu
                .active {
                    right: 0;
                }
                
                // responsive add jquery menu
                // social icones menu responsive
                .fa-facebook-square {
                    color: #3b5998;
                }
                
                .fa-instagram {
                    color: #8a3ab9;
                }
                
                .fa-whatsapp-square {
                    color: #34B7F1;
                }
                
                .fa-telegram {
                    color: #229ED9;
                }
                
                .close-btn {
                    position: absolute;
                    font-size: 20px;
                    right: 0;
                    cursor: pointer;
                    margin: 25px;
                    color: white;
                    i {
                        color: white;
                    }
